/* ======== UTILITIES ============ */

.bordered {
    border-left: 1px solid #393E41;
    border-right: 1px solid #393E41;
}

/* ======== UTILITIES ============ */

.app {
    font-family: sans-serif;
    background-color: white;
    margin: 0 auto;
    height: 100%;
    max-width: 768px;
}

@media all and (min-width: 768px) {
    .app {
        border-top-left-radius: 32px;
    }
}

.page {
    height: 100%;
}

/* =========== HERO ================= */

.hero {
    padding: 24px;
    min-height: 100%;
    overflow: scroll;
    display: flex;
    flex-direction: column;
    gap:16px;
}

.hero-img {
    position: relative;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('https://images.squarespace-cdn.com/content/v1/631ca456bc567e7e9af88cd4/1662822611040-NWWPB2XVOBQNR3L882OJ/unsplash-image-KLbUohEjb04.jpg');
    margin-top: auto;
    height: 50vh;
    min-height: 250px;
    border-bottom-right-radius: 32px;
}

.hero-img .bubble {
    top: -16px;
    left: -16px;
    height: 64px;
    width: 64px;
    border-radius: 50%;
    background-color: #E2C044;
    position: relative;
    box-shadow: 5px 3px 15px -5px rgba(0,0,0,0.75);
    -webkit-box-shadow: 5px 3px 15px -5px rgba(0,0,0,0.75);
    -moz-box-shadow: 5px 3px 15px -5px rgba(0,0,0,0.75);
}

.hero-heading {
    justify-content: center;
    flex-direction: column;
    display: flex;
    height: 25vh;
}

/* ============= HERO ============== */

/* ============= COPY ============== */

.copy {
    background-color: #587B7F;
    color: white;
    min-height: 100%;
    padding: 24px;
    font-size: 1.25rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;
}

.copy p {
    max-width: 500px;
}

/* ============= COPY ============== */

/* ============= SKILLS ============== */

.skills {
    min-height: 100%;
    background-color: #1E2019;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.logos-row {
    min-height: 125px;
    display: flex;
    justify-content: space-around;
}

.logos-row img {
    max-width: 100px;
}

/* ============= SKILLS ============== */

/* ============= CTA ============== */

.cta {
    overflow: hidden;
    display: flex;
    color: black;
    min-height: 100%;
}

.cta button {
    flex-grow: 1;
    border: none;
}

.cta button:hover {
    text-decoration: underline;
    cursor: pointer;
}

.cta>button:nth-child(1) {
    background-color: #E2C044;
}

.cta>button:nth-child(2) {
    background-color: #587B7F;
    color: white;
}

.cta>button:nth-child(3) {
    background-color: #393E41;
    color: white;
}

/* ============= CTA ============== */

